@import url(https://fonts.googleapis.com/css2?family=Inconsolata&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@300&display=swap);
.banner {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	height: 10rem;
}

.logo {
	height: 135px;
	width: auto;
}

@media all and (max-width: 360px) {
	.banner {
		height: 8rem;
	}
	.logo {
		height: 110px;
	}
}

/* NEED TO FIX 'ACTIVE' NAVLINKS COLOURING WHEN SCREEN SIZE IS SMALL */

.nav {
	position: -webkit-sticky;
	position: sticky;
	display: flex;
	flex-direction: row;
	justify-content: center;
	top: 0;
	left: 0;
	background: #6f5532; /* #4d4865 */
	height: 3rem;
	width: 100%;
	font-family: "Segoe UI";
	z-index: 1;
}

.nav ul {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: row;
	font-size: 1.3em;
	font-weight: 400;
	color: white;
	width: 100%;
}

.nav li {
	list-style: none;
}

.nav a {
	padding: 0.9rem 3rem 0.9rem 3rem;
	color: white;
	text-decoration: none;
}

.nav a:hover {
	background: white;
	color: black;
	cursor: pointer;
}

@media all and (max-width: 40em) {
	.nav ul {
		justify-content: space-around;
	}
	.nav a {
		padding: 0;
	}
	.nav a:hover {
		background: #6f5532;
	}
}

@media all and (max-width: 360px) {
	.nav a {
		font-size: 0.8em;
	}
}

.beer {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;
	padding: 0.5rem;
	max-height: 100%;
}

.beerimg {
	height: 12.5em;
	width: auto;
	flex-shrink: 0;
}

.beertitle {
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	align-items: center;
	padding: 1rem 1rem 0.2rem 1rem;
	width: 90%;
	font-family: "Segoe UI", "Helvetica";
}

.beerprice {
	font-size: 0.8rem;
}

.beerdesc {
	font-family: "Noto Sans JP", sans-serif;
	font-size: 0.8rem;
	margin: 0.5em;
}

.buttoncontainer {
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	height: 100%;
}

.beerbutton {
	margin: 1rem;
	padding: 0.5rem;
	border: 0.05rem solid black;
	font-family: "Noto Sans JP", sans-serif;
	font-size: 0.6rem;
	color: black;
	background: white;
	cursor: pointer;
}

.beerbutton:hover {
	background: #393939;
	color: white;
}

.beerbutton:active {
	background: white;
	color: #393939;
}

.beerbutton:focus {
	outline: none;
}

.beerbutton[disabled] {
	color: red;
	font-size: 0.8rem;
	background: #fff;
	border-style: none;
}

.store {
	display: flex;
	flex-direction: column;
	height: auto;
	background: white;
	align-items: center;
	padding: 3em 0 10em 0;
}

.container {
	display: grid;
	max-width: 80%;
	text-align: center;
	grid-template-columns: 1fr 1fr 1fr;
	justify-content: space-around;
	grid-gap: 3.5em;
}

@media all and (max-width: 52em) {
	.container {
		grid-template-columns: 1fr 1fr;
	}
}

@media all and (max-width: 40em) {
	.container {
		grid-template-columns: 1fr;
		grid-gap: 3em;
	}
}

/* CHECKOUT LANDING AND CONTAINER */

.checkout-wrapper {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
	height: 100%;
	width: 100%;
}

.checkout {
	margin-top: 1em;
	margin-bottom: 8em;
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 50%;
}

.checkout-title {
	display: none;
}

.checkout-total {
	width: 75%;
	text-align: center;
	border: 1px solid #393939;
	margin-top: 2.5em;
	background: white;
	border-radius: 4px;
}

.checkout-total > * {
	margin: 1em;
}

.checkout-button {
	padding: 0.5rem;
	border: 0.05rem solid #393939;
	font-family: "Segoe UI";
	font-size: 0.9rem;
	font-weight: 400;
	color: white;
	background: black;
	cursor: pointer;
	z-index: -1;
}

.checkout-button:hover {
	background: white;
	color: black;
}

.checkout-button:active {
	background: black;
	color: white;
}

.checkout-button:focus {
	outline: none;
}

/* INDIVIDUAL ITEMS IN CART */

.order-item {
	display: grid;
	grid-template-columns: 4fr 8fr 1fr 1fr;
	align-items: center;
	list-style: none;
	padding: 1em;
	border-bottom: 1px solid #393939;
	font-family: "Segoe UI";
}

.order-item:last-child {
	border-bottom: none;
}

.order-item-title {
	display: flex;
	align-items: center;
	font-weight: 700;
}

.order-item-title > * {
	margin-left: 0.5em;
}

.order-item img {
	height: 100px;
}

.order-item-price {
	text-align: center;
}

.order-item-button-container {
	display: flex;
	flex-direction: column;
}

.order-item-button {
	position: relative;
	left: 45%;
	border: 0;
	background: white;
	height: 1.5em;
	width: 1.5em;
	padding: 0;
}

.order-item-button:first-child,
.order-item-button:last-child {
	color: #393939;
	-webkit-transform: scale(0.75);
	        transform: scale(0.75);
}

.order-item-button:hover {
	background: black;
	color: white;
}

.order-item-button:active {
	background: white;
	color: black;
}

.order-item-button:focus {
	outline: none;
}

.empty-cart {
	margin-top: 2em;
}

@media all and (max-width: 90em) {
	.checkout {
		width: 60%;
	}
}

@media all and (max-width: 65em) {
	.checkout {
		width: 75%;
	}
}

@media all and (max-width: 38em) {
	.checkout {
		width: 95%;
	}
}

/* STILL NEED TO SORT OUT SCROLLING WHEN CART ITEM LSIT GETS LONGGGGGGGGGGGGGGGG */

/* RESET */

a {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

/* CART COMPONENT ELEMENTS */

.cart-svg {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	position: absolute;
	right: 100%;
	top: 50%;
	height: 4em;
	width: 2em;
	border-top: 1px solid black;
	border-left: 1px solid black;
	border-bottom: 1px solid black;
	background: #6f5532;
}

.cart-svg:hover {
	cursor: pointer;
}

.cart-wrapper {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;
	position: fixed;
	background: white;
	top: 0;
	right: 0;
	width: 30em;
	height: 100%;
	transition: all 0.5s cubic-bezier(0.49, 0.12, 0.02, 1);
	-webkit-transition: all 0.5s cubic-bezier(0.49, 0.12, 0.02, 1);
	transform: translateX(100%) translate3d(0, 0, 0);
	-webkit-transform: translateX(100%) translate3d(0, 0, 0);
	z-index: 2;
}

.cart-wrapper-active {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;
	position: fixed;
	background: white;
	bottom: 0;
	right: 0;
	width: 30%;
	height: 100%;
	transition: all 0.5s cubic-bezier(0.49, 0.12, 0.02, 1);
	-webkit-transition: all 0.5s cubic-bezier(0.49, 0.12, 0.02, 1);
	transform: translate3d(0, 0, 0);
	-webkit-transform: translate3d(0, 0, 0);
	border-left: 1px black solid;
	box-shadow: -1em 0em 20em -1em black;
	z-index: 2;
}

.cart {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
	height: 100%;
	width: 100%;
	overflow-y: hidden;
}

/* MODIFIED CHECKOUT ELEMENTS FOR CART */

.cart .checkout {
	justify-content: flex-start;
	width: 100%;
	height: 100%;
	margin: 0;
}

.cart .checkout-title {
	display: flex;
	width: 100%;
	height: 12%;
	justify-content: center;
	align-items: center;
	box-shadow: 1px 0 4px #6f5532;
	z-index: 2;
	color: #6f5532;
}

.cart .checkout-order {
	height: 100%;
	width: 95%;
	overflow-y: auto;
	z-index: 1;
}

.cart .order-item {
	display: grid;
	grid-template-columns: 5fr 1fr 1fr;
	grid-gap: 1em;
	padding: 1em;
	align-items: center;
	list-style: none;
	border-bottom: 1px solid black;
}

.cart .checkout-total {
	display: none;
}

.cart img {
	display: none;
}

/* BUTTONS */

.cart-button-wrapper {
	position: relative;
	bottom: 0;
	display: flex;
	flex-direction: column;
	justify-content: center;
	height: 6.5em;
	width: 100%;
	box-shadow: 4px 0px 4px #6f5532;
	z-index: 2;
}

.cart-button {
	position: fixed;
	bottom: 3%;
	padding: 0.8rem;
	border: 0.05rem solid #6f5532;
	font-family: "Noto Sans JP", sans-serif;
	font-size: 0.9rem;
	font-weight: 700;
	color: #6f5532;
	background: white;
	cursor: pointer;
	z-index: 3;
}

.cart-button:hover {
	background: #6f5532;
	color: white;
}

.cart-button:active {
	background: white;
	color: #6f5532;
}

.cart-button:focus {
	outline: none;
}

@media all and (max-width: 80em) {
	.cart-wrapper-active {
		width: 40%;
	}
}

@media all and (max-width: 65em) {
	.cart-wrapper-active {
		width: 55%;
	}
}

@media all and (max-width: 45em) {
	.cart-wrapper-active {
		width: 75%;
	}
}

* {
	margin: 0;
	padding: 0;
}

html,
body {
	margin: 0;
	padding: 0;
}

body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

